.App {
  text-align: center;
}

body{
  background: #FAFAFA;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

img {
  /* width: 100%; */
}

ul li {
  list-style: none;
}
a {
  color: inherit;
  text-decoration: none;
}
button {
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
}

.container {
  width: 100%;
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}

.app {
  margin: 0 auto;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
